// Todotemplate
$background-color:#22b8cf;

.TodoTemplate{
 width: 512px;
 margin: 6rem auto;
 border-radius: 4px;
 overflow: hidden;
 .app-title{
  background: $background-color;
  color: #fff;
  height: 4rem;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
 }
 .content{
  background: #fff;
 }
}